    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
		product() { return this.$store.getters.product },
		productId() { return this.$route.params.productId },
        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && checkNavIsShow(item.id);
            });

            return list;
        },
        isQzApp() {
            return !(typeof callNative == "undefined");
        },
        isIframeId() {
            return [10, 18].indexOf(Number(this.productId)) > -1
        },
		queryGameType() { return this.$route.query.gameType },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            
            if(this.user.roles.indexOf('user') > -1) {

                if(this.isIframeId && this.queryGameType) {
                    this.gameType = this.queryGameType;
                    const autoExchange = this.app.autoExchange;
                    if(autoExchange) {
                        this.reloadBalance((hadExchange) => {
                            if(hadExchange) {
                                const product_ids = this.filterProduct.map(p => { return p.id }).join(',');
                                this.$store.dispatch('productBalanceByAjax', { product_ids }).then(() => {
                                    this.getProductUrl((json) => {
                                        window.location.href = json.list.login_url;
                                    });
                                });
                            }else {
                                this.getProductUrl((json) => {
                                    window.location.href = json.list.login_url;
                                });
                            }
                        });
                    }else {
                        this.getProductUrl((json) => {
                            window.location.href = json.list.login_url;
                        });
                    }
                    
                    return ;
                }
    
                if(this.isIframeId) {
                    // if(this.app.hideLayout) {
                    //     this.checkActivity();
                    // }
                    return ;
                }
    
                this.getProductUrl();
                
            }else if(this.user.roles.indexOf('try') > -1) {
                
            }else {
              this.$router.push('/auth/login');
              return ;  
            }
        },

        getProductUrl(cb) {
            const data = {
                product_id: this.productId,
                gameversion: 2,
                orientation: 2,
                game_type: this.gameType,
            }

            // if(this.loading) return ;
            this.loading = true;
            getUrl(data).then(json => {
                this.loading = false;
				if (json.code == 200) {
                    if(cb) {
                        cb(json);
                    }else if(!this.isIframeId) {
                        window.location.href = json.list.login_url;
                    }else {
                        this.url = json.list.login_url.replace('http://', '//');
                    }
				} else {
                    this.showMaintain = true;
					// alert(json.info);
				}
			});
        },

        load() {
            this.loading = false;
        },


        KGClick(gameType) {
            this.gameType = gameType;

            if(!this.firstClick) {
                this.getProductUrl();
            }else {
                this.firstClick = false;

                this.gameType = gameType;
                // const router = this.$router.resolve({ path: '/redirect', query: { pid: this.productId, gameType }});
                // this.url = router.href;
                const autoExchange = this.app.autoExchange;

                if(autoExchange) {
                    this.reloadBalance((hadExchange) => {
                        if(hadExchange) {
                            const product_ids = this.filterProduct.map(p => { return p.id }).join(',');
                            this.$store.dispatch('productBalanceByAjax', { product_ids }).then(() => {
                                this.getProductUrl();
                            });
                        }else {
                            this.getProductUrl();
                        }
                    });
                }else {
                    this.getProductUrl();
                }
            }
            

            this.$scrollTo(document.querySelector('.layout_main'), 100)

            document.querySelector('.layout_main').scrollTop = 0;
        },

        getHeight() {
            let h = 'calc(100vh';

            if(!this.app.isApp) {
                if(!this.app.hideLayout) {
                    h += ' - 50px - 56px';
                }else if(this.app.hideLayout) {
                    h += ' - 50px';
                }
            }

            h += ')';

            return h;
        },

        pushPath(item) {
            const data = {
                product_id: this.productId,
                gameversion: 2,
                orientation: 2,
                game_type: item.id,
            }

            // const autoExchange = true;
            const autoExchange = this.app.autoExchange;

            if(autoExchange) {
                this.reloadBalance((hadExchange) => {
                    if(hadExchange) {
                        const product_ids = this.filterProduct.map(p => { return p.id }).join(',');
                        this.$store.dispatch('productBalanceByAjax', { product_ids }).then(() => {
                            this.getProductUrlByApp(data);
                        });
                    }else {
                        this.getProductUrlByApp(data);
                    }
                });
            }else {
                this.getProductUrlByApp(data);
            }
        },

        getProductUrlByApp(data) {
            getUrl(data).then(json => {
                this.loading = false;
                if (json.code == 200) {
                    let title = '游戏';

                    if(data.product_id == 10) {
                        this.kGList.forEach(item => {
                            if(item.id == data.game_type && item.name) {
                                title = item.name;
                            }
                        });
                    }

                    this.$store.dispatch('postMsgToApp', {
                        action: 'presentModal',
                        data: {
                            url: json.list.login_url,
                            title: title,
                        }
                    })
                } else {
                    this.showMaintain = true;
                    // alert(json.info);
                }
            });
        },

        reloadBalance(callback) {
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            this.loading = true;
            this.$store.dispatch('productBalanceByAjax', { product_ids }).then((json) => {
                if(json.code == 200) {
                    let idArr = [], total_money = 0;
                    for(let id in json.list) {
                        const money = Number(json.list[id].money);
                        
                        if(id != 0 && id != this.productId) {
                            // 大于 0 则 回收
                            if(money > 0) {
                                idArr.push(id);
                            }
                        }

                        if(id == 0) {
                            total_money = money
                        }
                    }

                    if(idArr.length) {
                        const data = {
                            type: 3,
                            product_id: idArr.join(','),
                        }

                        transfer(data).then(json => {
                            if (json.code == 200) {
                                transferallout(this.productId).then(() => {
                                    setTimeout(() => {
                                        this.loading = false;
                                        callback && callback(1);
                                    }, 3000);     
                                })
                            } else {
                                this.$message.error(json.info);
                                this.loading = false;
                                callback && callback();
                            }
                        });
                    }else {
                        if(total_money > 0) {
                            transferallout(this.productId).then(() => {
                                this.loading = false;
                                callback && callback();
                            })
                        }else {
                            this.loading = false;
                            callback && callback();
                        }
                    }

                }else {
                    this.$message.error(json.info);
                    this.loading = false;
                    callback && callback();
                }
            });
        },

        checkActivity() {
            activityList({ is_join: 'N', key: 'QZKGZS' }).then(json => {
                if(json.code == 200) {
                    if(json.list.items.length) {
                        this.activityId = json.list.items[0].id;
                        this.showActivity = true;
                    }
                }
            })
        },

        activityClick() {
            if(this.activityLoading) return ;

            this.activityLoading = true;
            
            userJoin({
                activity_id: this.activityId,
            }).then(json => {
                this.activityLoading = false;
                if(json.code == 200) {
                    window.location.reload();
                }else {
                    this.showActivity = false;
                }
            });
        },

        changeGameClick() {
            this.$scrollTo(this.$el.querySelector('.kg_list'), 0)
            document.querySelector('.el-main').scrollTop = (document.querySelector('#app').clientHeight - this.app.headerHeight);
        },
    }
}
</script>

